import clsx from 'clsx';
import React, { useEffect, useRef } from 'react';

import { useMode } from '../../providers/mode';

import { ReactComponent as Arrow } from '../../assets/icons/arrow.svg';
import { ReactComponent as Discord } from '../../assets/icons/discord.svg';
import { ReactComponent as Twitter } from '../../assets/icons/twitter.svg';

const Item = React.memo(
    React.forwardRef(({ title, description, photos, hrefs, data }, forwardedRef) => {
        const ref = useRef();
        const { isDark } = useMode();

        useEffect(() => {
            forwardedRef && forwardedRef(ref.current);
            return () => forwardedRef && forwardedRef(undefined);
        }, [forwardedRef]);

        return (
            <div className={clsx('item', isDark && 'item-dark')} ref={ref}>
                <div className="photos">
                    {photos.map((photo, index) => (
                        <div
                            key={index}
                            className="photo"
                            style={{
                                backgroundImage: `url(${photo})`,
                            }}
                        ></div>
                    ))}
                </div>
                <div className="item-main">
                    <div>
                        <div className="item-header">
                            <div className={clsx('item-title', isDark && 'item-title-dark')}>{title}</div>
                            <div className="item-menu">
                                <a
                                    href={hrefs?.website}
                                    className={clsx('item-btn', isDark && 'item-btn-dark')}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    <span>
                                        Website
                                        <Arrow />
                                    </span>
                                </a>
                                <a
                                    href={hrefs?.twitter}
                                    className="item-icon"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    <Twitter />
                                </a>
                                <a
                                    href={hrefs?.discord}
                                    className="item-icon"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    <Discord />
                                </a>
                            </div>
                        </div>
                        <div className={clsx('item-description', isDark && 'item-description-dark')}>{description}</div>
                    </div>
                    <div className="item-info">
                        <div className="item-info-col">
                            <div className="item-info-text-row">
                                {!!data.presale && (
                                    <div className={clsx('item-info-text', isDark && 'item-info-text-dark')}>
                                        Presale:
                                        <span> {data.presale} ETH</span>
                                    </div>
                                )}
                                <div className={clsx('item-info-text', isDark && 'item-info-text-dark')}>
                                    {!!data.presale && 'Sale:'}
                                    <span> {data.sale} ETH</span>
                                </div>
                            </div>
                            <div className={clsx('item-info-text', isDark && 'item-info-text-dark')}>10,000 total</div>
                        </div>
                        <div className="item-info-col">
                            {!!data.presaleDate && (
                                <div className={clsx('item-info-text', isDark && 'item-info-text-dark')}>
                                    Presale: {data.presaleDate}
                                </div>
                            )}
                            <div className={clsx('item-info-text', isDark && 'item-info-text-dark')}>
                                Sale: {data.saleDate}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    })
);

Item.displayName = 'Item';

export default Item;
