import { slice, concat } from 'lodash';

import React, { useEffect, useState } from 'react';
import { useInView } from 'react-intersection-observer';

import data from '../../data/data.json';
import { useMode } from '../../providers/mode';
import ItemsLoader from '../loaders/ItemsLoader';
import Item from './Item';

const DATA = data;
const LENGTH = data.length;
const LIMIT = 6;

export default function Sales() {
    const { ref, inView } = useInView({ threshold: 0 });

    const [items, setItems] = useState(slice(DATA, 0, LIMIT));
    const [isMoreDataAvailable, setIsMoreDataAvailable] = useState(false);
    const [arrayIndex, setArrayIndex] = useState(LIMIT);
    const [isLoading, setIsLoading] = useState(false);
    const { isDark } = useMode();

    useEffect(() => {
        items.length < LENGTH ? setIsMoreDataAvailable(true) : setIsMoreDataAvailable(false);
    }, [items]);

    useEffect(() => {
        const loadMore = () => {
            setIsLoading(true);
            const newArrayIndex = arrayIndex + LIMIT;
            const newItems = concat(items, slice(DATA, arrayIndex, newArrayIndex));
            setArrayIndex(newArrayIndex);
            setItems(newItems);
            setIsLoading(false);
        };

        inView && isMoreDataAvailable && loadMore();
    }, [inView, isMoreDataAvailable, arrayIndex, items]);

    const lastId = items.length && items[items.length - 1].id;

    return (
        <div>
            <div className={`list-box-title ${isDark ? 'list-box-title-dark' : ''}`}>Tomorrow&apos;s Sales</div>
            <div className="items">
                {!!items.length &&
                    items.map((item, index) => (
                        <Item
                            key={index}
                            ref={item.id === lastId ? ref : undefined}
                            title={item.title}
                            description={item.description}
                            photos={item.photos}
                            hrefs={item.hrefs}
                            data={item.data}
                        />
                    ))}
                {isLoading && isMoreDataAvailable && <ItemsLoader />}
            </div>
        </div>
    );
}
