import clsx from 'clsx';
import React from 'react';

import { useMode } from '../../providers/mode';

import { ReactComponent as Logo } from '../../assets/icons/logo.svg';
import { ReactComponent as Moon } from '../../assets/icons/moon.svg';
import { ReactComponent as Sun } from '../../assets/icons/sun.svg';

export default function Header() {
    const { isDark, setIsDark } = useMode();

    return (
        <div className="header">
            <div className="logo">
                <Logo />
                <span className={clsx('logo-title', isDark && 'logo-title-dark')}>stat.tools</span>
            </div>
            <div className="menu">
                <span className={clsx('nav', isDark && 'nav-dark')}>Contact us</span>
                <button
                    type="button"
                    aria-label="set-site-mode"
                    onClick={setIsDark}
                    className={clsx('btn-mode', isDark && 'btn-mode-dark')}
                >
                    {isDark ? <Sun /> : <Moon />}
                </button>
                <span className={clsx('btn-menu', isDark && 'btn-menu-dark')}>
                    <span>List my project</span>
                </span>
            </div>
        </div>
    );
}
