import clsx from 'clsx';
import React from 'react';

import { useMode } from '../../providers/mode';

export default function Title() {
    const { isDark } = useMode();

    return (
        <div className={clsx('title-wrapper', isDark && 'title-wrapper-dark')}>
            <div className="title-box">
                <h1 className={clsx('title', isDark && 'title-dark')}>Upcoming NFT Sales</h1>
                <p className={clsx('description', isDark && 'description-dark')}>
                    Disclaimer: Any information provided on this page including the featured project(s) should not be
                    considered as financial or investment advice. The information is provided as-is with no guarantees.
                    We cannot validate the claims or the trustworthiness of the projects listed. By using this
                    information you agree to not take rarity.tools responsible for the results of your investment
                    decisions resulting from it. Please do your own research!
                </p>
                <span className={clsx('title-box-btn', isDark && 'title-box-btn-dark')}>
                    Submit my project for the list
                </span>
            </div>
        </div>
    );
}
